height: 1.5;\n$line-height-s: 1.3;\n\n/**\n * Grid.\n */\n$grid-columns: 12;\n$gutter: 30px;\n\n/**\n * Spacing.\n */\n$space: 20px;\n\n/**\n * Spacing - Times.\n */\n$space-and-half: $space * 1.5;\n$space-double: $space * 2;\n$space-triple: $space * 3;\n$space-quadruple: $space * 4;\n$space-quintuple: $space * 5;\n$space-sextuple: $space * 6;\n$space-septuple: $space * 7;\n\n/**\n * Spacing - Divided.\n */\n$space-three-fourth: $space * 0.75;\n$space-one-half: $space / 2;\n$space-half: $space-one-half;\n$space-one-third: $space / 3;\n$space-third: $space-one-third;\n$space-one-fourth: $space / 4;\n$space-fourth: $space-one-fourth;\n$space-quarter: $space-one-fourth;\n$space-one-fifth: $space / 5;\n$space-one-sixth: $space / 6;\n$space-one-seventh: $space / 7;\n$space-one-eighth: $space / 8;\n\n/**\n * Common breakpoints.\n */\n$xsmall: 320px;\n$small: 500px;\n$medium: 720px;\n$large: 960px;\n$xlarge: 1220px;\n$xxlarge: 1440px;\n\n$breakpoints: (\"xsmall\": $xsmall, \"small\": $small, \"medium\": $medium, \"large\": $large, \"xlarge\": $xlarge, \"xxlarge\": $xxlarge);\n\n/**\n * Max width.\n */\n$max-width: $xlarge;\n$max-width-s: 600px;\n$max-width-m: 800px;\n$max-width-l: $large;\n$max-width-xl: $xlarge;\n$max-width-xxl: $xxlarge;\n\n/**\n * Icons.\n */\n$icon-xsmall: 10px;\n$icon-small: 20px;\n$icon-medium: 30px;\n$icon-large: 40px;\n$icon-xlarge: 50px;\n$icon-xxlarge: 90px;\n\n/**\n * Border/Shadow Styles.\n */\n$border-radius-curve: 2px;\n$border-radius-curve--hard: 4px;\n$border--standard: 1px solid $c-gray;\n$border--dotted: 1px dashed $c-gray;\n$border--thick: 6px solid $c-primary;\n$box-shadow--small: 0 0 5px rgba(black, 0.3);\n$box-shadow: 0 10px 15px rgba(black, 0.15);\n$outline: 3px solid $c-primary;\n\n/**\n * Transitions.\n */\n$transition-effect: cubic-bezier(0.86, 0, 0.07, 1);\n$transition-all: all 0.25s $transition-effect;\n\n/**\n * Z Index.\n */\n$z-index-vanish: -1;\n$z-index-none: 0;\n$z-index-1: 100;\n$z-index-2: 200;\n$z-index-5: 500;\n$z-index-10: 1000;\n$z-index-15: 1500;\n$z-index-30: 3000;\n$z-index-50: 5000;\n$z-index-75: 7500;\n$z-index-100: 10000;\n\n$z-index-mq-display: $z-index-100;\n$z-index-menu-toggle: $z-index-100;\n$z-index-back-to-top: $z-index-100;\n\n/*\n * Theme variables.\n */\n$header-utility-height: 40px;\n$header-height: 70px;\n$footer-logo: 150px;\n$sidebar-width: 300px;\n$main-width: calc(100% - (#{$sidebar-width}));\n$image-path: '/wp-content/themes/cahillscreative/resources/assets/images/';\n","/*\n * General Mixins (put specific ones in component files where applicable)\n*/\n\n@mixin body-copy {\n  font-family: $font-body;\n  font-size: $body-font-size;\n  line-height: $line-height;\n  letter-spacing: 0.25px;\n}\n\np {\n  @include body-copy;\n}\n\n@mixin u-font--m {\n  font-family: $font-body;\n  font-size: $font-size-m;\n  line-height: $line-height;\n}\n\n.u-font--m {\n  @include u-font--m;\n}\n\n@mixin u-font--s {\n  font-family: $font-body;\n  font-size: $font-size-s;\n  line-height: $line-height-s;\n}\n\n.u-font--s {\n  @include u-font--s;\n}\n\n@mixin u-font--xs {\n  font-family: $font-body;\n  font-size: $font-size-xs;\n  line-height: $line-height;\n}\n\n.u-font--xs {\n  @include u-font--xs;\n}\n\n/*\n * Clearfix.\n * Adds clearfix based on http://bourbon.io/docs/#clearfix.\n*/\n@mixin clearfix {\n  &::after {\n    clear: both;\n    content: '';\n    display: table;\n  }\n}\n\n/*\n * Aspect Ratio.\n*/\n@mixin aspect-ratio($width, $height) {\n  position: relative;\n\n  &::before {\n    display: block;\n    content: \"\";\n    width: 100%;\n    padding-top: $height / $width * 100%;\n    pointer-events: none;\n  }\n\n  > * {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n  }\n}\n\n/*\n * Align center.\n*/\n@mixin align-center {\n  margin-left: auto;\n  margin-right: auto;\n  position: relative;\n}\n\n/*\n * Wrapper.\n * Outer container mixin for large screens.\n*/\n@mixin wrapper($max-width: $max-width-xl, $breakpoint: \">large\", $v-margin: 0, $v-padding: 0, $h-padding: $space, $h-padding-large: $space-double) {\n  max-width: #{$max-width};\n  width: 100%;\n  margin: #{$v-margin} auto;\n  padding: #{$v-padding} #{$h-padding};\n\n  @include media($breakpoint) {\n    padding-left: #{$h-padding-large};\n    padding-right: #{$h-padding-large};\n  }\n}\n\n/*\n * Colors.\n*/\n$colors: (\"primary\": $c-primary, \"secondary\": $c-secondary, \"tertiary\": $c-tertiary, \"white\": $c-white, \"black\": $c-black, \"gray\": $c-gray);\n\n$styles: (\"background-color\": \"background-color\", \"color\": \"color\", \"path-fill\": \"fill\");\n\n@each $style_key, $style_value in $styles {\n  @each $color_key, $color_value in $colors {\n    .u-#{$style_key}--#{$color_key} {\n      #{$style_value}: #{$color_value};\n    }\n  }\n}\n","*,\n*::after,\n*::before {\n  box-sizing: inherit;\n  margin: 0;\n  padding: 0;\n}\n\nhtml {\n  box-sizing: border-box;\n}\n\nbody {\n  font-family: $font-body;\n  font-size: $body-font-size;\n  background: $c-white;\n  color: $c-black;\n\n  &::before {\n    content: \"\";\n    display: block;\n    width: 100vw;\n    height: 100vh;\n    opacity: 0;\n    visibility: hidden;\n    background: white;\n    z-index: 1;\n    position: fixed;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    transition: $transition-all;\n  }\n\n  &::after {\n    content: \"\";\n    display: block;\n    width: 1px;\n    height: 100vh;\n    background-color: $c-gray;\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    margin: auto;\n    z-index: -1;\n  }\n}\n\na {\n  text-decoration: none;\n  transition: $transition-all;\n  color: inherit;\n\n  svg path {\n    fill: currentColor;\n  }\n}\n\nimg,\nvideo,\nobject,\nsvg,\niframe {\n  max-width: 100%;\n  border: none;\n  display: block;\n}\n\nstrong,\nb {\n  font-family: 'GT Walsheim Medium';\n}","/*\n * Generic header styles:\n * All arguments are optional. If not defined, the defaults below will be used\n*/\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  